* {
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}
input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-right: 10px;
  margin-bottom: 10px;

}

.card p {
  padding: 5px;
}

.card img{
  max-height: 170px;
}
